import { ref } from 'vue';
import { useRoute, useRouter } from 'vue-router';
export default {
  name: 'NavMenu',
  setup() {
    let route = useRoute();
    let activeIndex = ref(route.path);
    return {
      activeIndex
    };
  },
  watch: {
    $route() {
      this.activeIndex = this.$route.path;
    }
  },
  mounted() {}
};